  font-size: 1.2rem;
}
#description {
  border: 5px solid #275373;
  width: 100%;
  color: #275373;
  font-weight: 700;
  font-size: 1.5rem;
  padding: 20px;
  box-shadow: 8px 8px #275373;
  transition: all 0.3s ease;
}
#description:focus {
  box-shadow: 0px 0px #275373;
}
.saveBtn {
  font-size: 1.7rem;
  padding: 20px;
  border: none;
  background-color: #275373;
  color: white;
  float: right;
  outline: none;
  cursor: pointer;
  transition: all 0.2s;
}
.lang-sel {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.saveBtn:hover {
  box-shadow: 5px 5px #275373;
}
.saveBtn:active {
  box-shadow: 0px 0px #275373;
  transform: translate(5px, 5px);
}
.saveBtn svg {
  margin-left: 20px;
  width: 2.5rem;
  vertical-align: middle;
  fill: white;
}
.CodeMirror {
  font-family: "Fira Code", monospace !important;
  font-size: 1rem !important;
  height: 70vh !important;
}

@media only screen and (max-width: 768px) {
  .heading {
    font-size: 1.5rem;
  }
  .heading svg {
    max-width: 2rem;
    margin-left: 15px;
  }
  .select-language-group label {
    font-size: 1rem;
  }
  .title-group label {
    font-size: 1.1rem;
  }
  #title {
    font-size: 1rem;
    padding: 5px 10px;
    border: 3px solid #275373;
  }
  .CodeMirror {
    font-size: 0.9rem !important;
  }
  .newsnippet {
    padding: 20px;
  }
  #description {
    padding: 10px;
    font-size: 1rem;
  }
}

.language-selector {
  position: relative;
}
.ls-button {
  padding: 10px 25px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 700;
  color: #275373;
  background-color: white;
  border: 3px solid #275373;
  box-shadow: 5px 5px #275373;
  /* border-radius: 4px; */
  outline: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.ls-button-svg {
  fill: #275373;
  vertical-align: middle;
  max-width: 1rem;
  float: right;
  margin-left: 25px;
  transition: all 0.3s ease;
}
.ls-button-svg.open {
  transform: rotate(-180deg);
}
.ls-button svg {
  max-width: 20px;
  min-width: 20px;
  vertical-align: middle;
}
.ls-ul {
  list-style: none;
  margin: 0px;
  padding: 0px;
  border: 3px solid #275373;
  min-width: 280px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  /* border-radius: 4px; */
  -webkit-animation: fade-in-ls 0.3s ease;
          animation: fade-in-ls 0.3s ease;
  overflow: hidden;
  position: absolute;
  top: calc(100% + 10px);
  z-index: 1000;
  box-shadow: 5px 5px #275373;
}
@-webkit-keyframes fade-in-ls {
  from {
    opacity: 0;
    transform: translateY(-15px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes fade-in-ls {
  from {
    opacity: 0;
    transform: translateY(-15px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}

.ls-li {
  padding: 15px 20px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 700;
  color: #275373;
  background-color: white;
  border-bottom: 2px solid #275373;
  transition: all 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.ls-li:last-of-type {
  border: 0px;
}
.ls-li:hover {
  background-color: #275373;
  color: white;
}
.ls-li svg {
  max-width: 20px;
  vertical-align: middle;
  margin-right: 10px;
}

.snippet-preview {
  border: 5px solid #275373;
  padding: 0px 30px;
  text-decoration: none;
}
.snippet-name {
  color: #275373;
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.snippet-name::after {
  content: "";
  position: absolute;
  background-color: #275373;
  width: 100%;
  height: 4px;
  bottom: -10px;
  left: 0;
}
.snippet-code {
  height: auto;
  background-color: #f2faff;
  margin-top: 30px;
}
.snippet-buttons {
  display: flex;
  flex-wrap: wrap;
}
.snippet-buttons a {
  text-decoration: none;
}
.snippet-buttons button,
.snippet-buttons a {
  padding: 8px 10px;
  font-weight: 700;
  color: #275373;
  background: none;
  border: 3px solid #275373;
  font-size: 1rem;
  cursor: pointer;
  margin-right: 15px;
  transition: all 0.2s;
}
.snippet-buttons button:hover,
.snippet-buttons a:hover {
  transform: scale(1.05);
}
.snippet-buttons button:nth-of-type(1),
.snippet-buttons .view {
  background-color: #275373;
  color: white;
  text-transform: capitalize;
  letter-spacing: 1px;
}
.snippet-buttons button:nth-of-type(1) {
  pointer-events: none;
}
.snippet-buttons .view svg {
  fill: white;
}

.snippet-buttons button svg,
.snippet-buttons a svg {
  vertical-align: middle;
  height: 1rem;
  fill: #275373;
}
.sn-bt-text {
  margin-right: 5px;
}
.snippet-date {
  color: #275373;
}
@media only screen and (max-width: 768px) {
  .sn-bt-text {
    display: none;
  }
  .snippet-buttons {
    margin-top: 0px;
  }
  .snippet-buttons button {
    margin-top: 20px;
  }
  .snippet-name {
    font-size: 1.2rem;
  }
}
.snippet-preview .CodeMirror {
  font-family: "Fira Code", monospace !important;
  font-size: 1rem !important;
  max-height: 350px;
  pointer-events: none;
}

