.navbar {
  background-color: #193549;
  position: fixed;
  box-sizing: content-box;
  left: 0;
  width: 8rem;
  transition: width 200ms ease;
  z-index: 100;
}
.navbar ul {
  padding: 0px;
  margin: 0px;
  height: 100%;
  /* width: 100%; TODO:change thsi after */
  display: flex;
  flex-direction: column;
  list-style: none;
  align-items: center;
}
.navbar ul li {
  height: 5rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 10px 0px;
  transition: background-color 400ms ease;
}
.navbar ul li:first-of-type a {
  justify-content: center;
  font-size: 2.3rem;
}
.navbar ul li:last-child {
  margin-top: auto;
  margin-bottom: 15px;
}

.navbar ul li a {
  color: #8db9d8;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.5rem;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  transition: background-color 0.3s ease;
}

.navbar .link-text {
  margin-left: 1rem;
  display: none;
}

.navbar .nav-selected {
  color: white;
}
.navbar .nav-selected .nav-icon circle {
  fill: #9effff;
  transition: all 0.3s ease;
}
.navbar .nav-selected .nav-icon path {
  fill: #275373;
  transition: all 0.3s ease;
}
.navbar .nav-selected .nav-icon line {
  stroke: #275373;
  transition: all 0.3s ease;
}
.n-tag {
