}
.n-bell {
  transform-origin: top center;
  -webkit-animation: swing 1s infinite ease-in-out;
          animation: swing 1s infinite ease-in-out;
}
@media only screen and (min-width: 769px) {
  .navbar {
    top: 0px;
    height: 100vh;
  }
