@keyframes swing {
  20% {
    transform: translate(17px, 19px) rotate3d(0, 0, 1, 15deg);
  }

  40% {
    transform: translate(17px, 19px) rotate3d(0, 0, 1, -10deg);
  }

  60% {
    transform: translate(17px, 19px) rotate3d(0, 0, 1, 5deg);
  }

  80% {
    transform: translate(17px, 19px) rotate3d(0, 0, 1, -5deg);
  }

  to {
    transform: translate(17px, 19px) rotate3d(0, 0, 1, 0deg);
  }
}
@-webkit-keyframes swing {
  20% {
    transform: translate(17px, 19px) rotate3d(0, 0, 1, 15deg);
